//////////////////////
// colors
//////////////////////

// greys

@white: #fff;
@black: #343a40;
@grey-250: #edf3f6;
@grey-500: #626c76;
@grey-750: #343a40;

// text

@text: @grey-750;
@text-inverse: #fff;

// colors

@primary: #7367F0; // 8C1BAB
@secondary: #d4e7ef;
@success: #28a745;
@notice: #17a2b8;
@warning: #ffc107;
@error: #dc3545;

// gradients

@grad-primary: linear-gradient(0deg, darken(@primary, 2%) 10%, @primary 100%);

//////////////////////
// fonts
//////////////////////

@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,600);

.font-sans-light() {
  font-family: 'Open Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-style: normal;
  font-weight: 300;
}

.font-sans-regular() {
  font-family: 'Open Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-weight: 400;
  font-style: normal;
}

.font-sans-semibold() {
  font-family: 'Open Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-weight: 600;
  font-style: normal;
}

.font-monospace() {
  font-family: monaco, Consolas, 'Lucida Console', monospace;
  font-weight: normal;
  font-style: normal;
}

//////////////////////
// grid
//////////////////////

@columns: 12;

// container

@containers: none 100%, xs 540px, sm 720px, md 960px, lg 1140px, xl 1300px; // container size
@container-fluid: 1300px; // container fluid size
@container-space: none 15px, xs 15px, sm 20px, md 25px, lg 30px, xl 30px; // spacing outside container

// breakpoints

@breakpoints: xs, sm, md, lg, xl; // if you change this change also variables names below
@breakpoint-xs: 576px;
@breakpoint-sm: 768px;
@breakpoint-md: 992px;
@breakpoint-lg: 1200px;
@breakpoint-xl: 1350px;

// @media queries
// USAGE: @media @min-sm and @max-md {}

@min-xs: ~'(min-width:'(@breakpoint-xs)~')';
@max-xs: ~'(max-width:'(@breakpoint-xs - 1)~')';
@min-sm: ~'(min-width:'(@breakpoint-sm)~')';
@max-sm: ~'(max-width:'(@breakpoint-sm - 1)~')';
@min-md: ~'(min-width:'(@breakpoint-md)~')';
@max-md: ~'(max-width:'(@breakpoint-md - 1)~')';
@min-lg: ~'(min-width:'(@breakpoint-lg)~')';
@max-lg: ~'(max-width:'(@breakpoint-lg - 1)~')';
@min-xl: ~'(min-width:'(@breakpoint-xl)~')';
@max-xl: ~'(max-width:'(@breakpoint-xl - 1)~')';

//////////////////////
// utils
//////////////////////

@margins: auto auto, none 0, tiny .5rem, small 1rem, medium 2rem, big 4rem, giant 6rem;
@paddings: auto auto, none 0, tiny .5rem, small 1rem, medium 2rem, big 4rem, giant 6rem;

@orders: 0, 1, 2, 3, 4, 5;
@core-indexes: btn-active 1, loader 11, scroll 100, backdrop 130, drop 150, overlay 200, close 300; // z-index values

//////////////////////
// layout
//////////////////////

// flex

@flex-space-horizontal: none 0, tiny .25rem, small .5rem, medium 1rem, big 2rem, giant 3rem;
@flex-space-vertical: none 0, tiny .25rem, small .5rem, medium 1rem, big 2rem, giant 3rem;

// list

@list-space-horizontal: none 0, tiny .25rem, small .5rem, medium 1rem, big 2rem, giant 3rem;
@list-space-vertical: none 0, tiny .25rem, small .5rem, medium 1rem, big 2rem, giant 3rem;

//////////////////////
// components
//////////////////////

@border-radius: 3px;
@backdrop-opacity: .6;

// btn

@btn-border-width: 1px;

// drop

@drop-space: 6px; // space between button and drop

// overlay

@overlays: tiny 540px, small 720px, medium 960px, big 1140px, giant 1300px; // overlay size
@overlay-fluid: 1300px; // overlay fluid size
@overlay-space: none 15px, xs 15px, sm 20px, md 25px, lg 30px, xl 30px; // space outside overlay

//////////////////////
// animations
//////////////////////

// size

@anim-tiny: .5rem;
@anim-small: .75rem;
@anim-medium: 1.5rem;
@anim-big: 3rem;
@anim-giant: 6rem;

// time

@time-tiny: 75ms;
@time-small: 150ms;
@time-medium: 300ms;
@time-big: 600ms;
@time-giant: 1200ms;

// beziers

@bez: @bezier-swoosh;
@bez-in: @bezier-swoosh-in;
@bez-out: @bezier-swoosh-out;

@bez-0: cubic-bezier(.63, .36, 0, .97);
@bezier-0: cubic-bezier(0, 1, .5, 1);
@bezier-1: cubic-bezier(0.19, 1, 0.22, 1);
@bezier-2: cubic-bezier(.25, .1, .25, 1);
@bezier-3: cubic-bezier(.79, .1, .2, .9);
@bezier-4: cubic-bezier(.63, .36, 0, .97);
@bezier-5: cubic-bezier(0, .92, .03, .99);
@bezier-6: cubic-bezier(.55, 0, .32, 1);
@bezier-7: cubic-bezier(.24, .66, .34, .73);
@bezier-8: cubic-bezier(.65, .04, 1, .76);
@bezier-9: cubic-bezier(.14, .68, .64, .99);
@bezier-10: cubic-bezier(.8, .1, .94, .52);
@bezier-11: cubic-bezier(.23, .72, .7, .2);
@bezier-12: cubic-bezier(1, 0, .33, .33);
@bezier-13: cubic-bezier(.73, .60, 0, 1);
@bezier-14: cubic-bezier(.64, .01, .98, .38);
@bezier-15: cubic-bezier(0, .63, .41, .98);
@bezier-16: cubic-bezier(.54, .2, .78, .41);
@bezier-17: cubic-bezier(.28, .56, .37, .74);
@bezier-18: cubic-bezier(.72, .31, .17, .9);
@bezier-bounce: cubic-bezier(0, 1.8, 1, 1.8);
@bezier-bounce-soft: cubic-bezier(0, 1.3, 1, 1.3);
@bezier-slide: cubic-bezier(.69, .01, .21, .99);
@bezier-swoosh: cubic-bezier(.96, .1, .14, .88);
@bezier-swoosh-in: cubic-bezier(0.645, 0.045, 0.355, 1.000);
@bezier-swoosh-out: cubic-bezier(0.550, 0.055, 0.675, 0.190);
@bezier-swoosh-delay: cubic-bezier(0.215, 0.610, 0.355, 1.000);
