//////////////////////
// contain
//////////////////////

.container,
.container-fluid {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.container-fluid {
  max-width: @container-fluid;
}

//////////////////////
// grid
//////////////////////

.col {
  flex: 1 1 100%;
  -webkit-flex-basis: auto;
  -ms-flex-preferred-size: 100%; // fix ie10 overflow
  flex-basis: auto;
  max-width: none;
  width: auto;
}

// grid

.col(@number, @breakpoint:none) when (@breakpoint = none) {
  @val: (percentage(@number / @columns));
  -webkit-flex-basis: @val;
  -ms-flex-preferred-size: @val;
  flex-basis: @val;
  max-width: @val;
  width: @val;
}

.col(@number, @breakpoint:none) when (default()) {
  @query-min: 'min-@{breakpoint}';
  @val: (percentage(@number / @columns));
  @media @@query-min {
    -webkit-flex-basis: @val;
    -ms-flex-preferred-size: @val;
    flex-basis: @val;
    max-width: @val;
    width: @val;
  }
}

//////////////////////
// flex
//////////////////////

.flex {
  .display(flex);
}

//////////////////////
// list
//////////////////////

.list-base() {
  .display(flex);
  //.flex-auto(); // @FIX flex inside flex take 100% but dont bugs negative margins
  // list
  .list-btn({
    .block();
    .text(left);
  });
  .list-btn-default({
    .btn();
    .text(left);
  });
}

// selectors

.list-btn(@rules) {
  > li  {
    > a, > button {
      @rules();
    }
  }
}
.list-btn-default(@rules) {
  > li  {
    > a, > button {
      &:not(.btn) {
        @rules();
      }
    }
  }
}

// .list-block

.list-block {
  .list-base();
  .flex-column();
  .flex-nowrap();
}

// .list-line

.list-line {
  .list-base();
  .flex-row();
  .flex-wrap();
}

// alignment

.list-left {
  .list-btn({
    .text(left) !important;
  });
}
.list-center {
  .list-btn({
    .text(center) !important;
  });
}
.list-right {
  .list-btn({
    .text(right) !important;
  });
}

//////////////////////
// box
//////////////////////

.box {
  .display(flex);
  .flex-column();
  position: relative;
}

