// display
// USAGE: .display(none); .display(none, sm);

.display(@val, @breakpoint:none) when (@breakpoint = none) {
  & when not (@val = flex), not (@val = inline-flex) {
    display: @val;
  }
  & when (@val = flex) {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
  }
  & when (@val = inline-flex) {
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
  }
}

.display(@val, @breakpoint:none) when (default()) {
  @query-min: 'min-@{breakpoint}';
  @media @@query-min {
    & when not (@val = flex), not (@val = inline-flex) {
      display: @val;
    }
    
    & when (@val = flex) {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
    }
    
    & when (@val = inline-flex) {
      display: -webkit-inline-box;
      display: -ms-inline-flexbox;
      display: inline-flex;
    }
  }
}

